"use client";
import { commentDbInterface } from "@/interfaces/post/user";
import { useAppSelector } from "@/redux/hook";
import { RootState } from "@/redux/store";
import { commentDbSchema } from "@/schema/post/post";
import { zodResolver } from "@hookform/resolvers/zod";
import { useState } from "react";
import { useForm, Controller, SubmitHandler } from "react-hook-form";

const Comment = ({ postId }: { postId: string }) => {
    const loggedInUser = useAppSelector((state: RootState) => state.currentUser);
    const [allComment, setAllComment] = useState<commentDbInterface[]>([]);

    const { control, handleSubmit, reset } = useForm({
        resolver: zodResolver(commentDbSchema),
        defaultValues: {
            //static
            postId,
            displayName: loggedInUser.displayName,
            photoURL: loggedInUser.photoURL,
            authorUID: loggedInUser.uid,
            //dynamic
            parentId: null,
            time: Date.now(),
            text: ""
        }
    });

    return (
        <div>
            {allComment.length ?
                <CommentView comment={allComment} />
                : "No comments"
            }
        </div>
    );
}

const CommentView = ({ comment }: { comment: commentDbInterface[] }) => {
    return (
        <>
            {JSON.stringify(comment)}
        </>
    );
}

export default Comment;